form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  width:100%;
  margin:20px;
}

input {
  width: 50%;
}

.formComponent {
  display: flex;
  flex-direction: column;
  width: 25vw;
  overflow:scroll;
  overflow-x:hidden;
}

.general,
.educational,
.experienceForm {
  height: 33vh;
  text-align: center;
  background-color: #7e7e7e;

  font-size: 1.5rem;
}


button{
  height:36px;
  width:25%;
  margin: 15px auto;
  border:none;
  border-radius: 5%;
  font-weight:bold;
}

.general h1,
.educational h1,
.experienceForm h1{
font-size: 1.953rem;
color:white;
}

.general form,.educational form,.experienceForm form{
  margin:0px auto;
}

.dutiesForm{
  width:90%;
  box-sizing: border-box;
  height:100%;
}
.experienceForm form button{
  margin: 30px auto;
}
input{
  width:90%;
  height:24px;
  font-size: large;
}



.general input, .educational input{
  height:36px;
}